@font-face {
  font-family: "lg";
  src: url("../plugins/gallery/fonts/lg.eot?n1z373");
  src: url("../plugins/gallery/fonts/lg.eot?#iefixn1z373") format("embedded-opentype"), url("../plugins/gallery/fonts/lg.woff?n1z373") format("woff"),
      url("../plugins/gallery/fonts/lg.ttf?n1z373") format("truetype"), url("../plugins/gallery/fonts/lg.svg?n1z373#lg") format("svg");
  font-weight: normal;
  font-style: normal;
}

.lg-icon {
  font-family: "lg";
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.lg-actions {
  .lg-next,
  .lg-prev {
      border: 1px solid $primary-09;
      background-color: $primary-1;
      border-radius: 5px;
      color: $white;
      cursor: pointer;
      display: block;
      font-size: 22px;
      margin-top: -10px;
      padding: 8px 10px 9px;
      position: absolute;
      top: 50%;
      z-index: 1080;
  }

  .lg-next.disabled,
  .lg-prev.disabled {
      pointer-events: none;
      opacity: 0.5;
  }

  .lg-next:hover,
  .lg-prev:hover {
      color: $white;
  }

  .lg-next {
      right: 20px;

      &:before {
          content: "\e095";
      }
  }

  .lg-prev {
      left: 20px;

      &:after {
          content: "\e094";
      }
  }
}

@-webkit-keyframes lg-right-end {
  0% {
      left: 0;
  }

  50% {
      left: -30px;
  }

  100% {
      left: 0;
  }
}

@-moz-keyframes lg-right-end {
  0% {
      left: 0;
  }

  50% {
      left: -30px;
  }

  100% {
      left: 0;
  }
}

@-ms-keyframes lg-right-end {
  0% {
      left: 0;
  }

  50% {
      left: -30px;
  }

  100% {
      left: 0;
  }
}

@keyframes lg-right-end {
  0% {
      left: 0;
  }

  50% {
      left: -30px;
  }

  100% {
      left: 0;
  }
}

@-webkit-keyframes lg-left-end {
  0% {
      left: 0;
  }

  50% {
      left: 30px;
  }

  100% {
      left: 0;
  }
}

@-moz-keyframes lg-left-end {
  0% {
      left: 0;
  }

  50% {
      left: 30px;
  }

  100% {
