}

.lg-outer {
  &.lg-right-end .lg-object {
      -webkit-animation: lg-right-end 0.3s;
      -o-animation: lg-right-end 0.3s;
      animation: lg-right-end 0.3s;
      position: relative;
  }

  &.lg-left-end .lg-object {
      -webkit-animation: lg-left-end 0.3s;
      -o-animation: lg-left-end 0.3s;
      animation: lg-left-end 0.3s;
      position: relative;
  }
}

.lg-toolbar {
  z-index: 1082;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  background-color: $primary-1;

  .lg-icon {
      color: $white;
      cursor: pointer;
      float: right;
      font-size: 24px;
      height: 47px;
      line-height: 27px;
      padding: 10px 0;
      text-align: center;
      width: 50px;
      text-decoration: none !important;
      outline: medium none;
      -webkit-transition: color 0.2s linear;
      -o-transition: color 0.2s linear;
      transition: color 0.2s linear;

      &:hover {
          color: $white;
      }
  }

  .lg-close:after {
      content: "\e070";
  }

  .lg-download:after {
      content: "\e0f2";
  }
}

.lg-sub-html {
  background-color: rgba(0, 0, 0, 0.45);
  bottom: 0;
  color: #eee;
  font-size: 16px;
  left: 0;
  padding: 10px 40px;
  position: fixed;
  right: 0;
  text-align: center;
  z-index: 1080;

  h4 {
      margin: 0;
      font-size: 16px;
      font-weight: 500;
  }

  p {
      font-size: 12px;
      margin: 5px 0 0;
  }
}

#lg-counter {
  color: $white;
  display: inline-block;
  font-size: 16px;
  padding-left: 20px;
  padding-top: 12px;
  vertical-align: middle;
}

.lg-toolbar,
.lg-prev,
.lg-next {
  opacity: 1;
  -webkit-transition: -webkit-transform 0.35s cubic-bezier(0, 0, 0.25, 1) 0s,
      opacity 0.35s cubic-bezier(0, 0, 0.25, 1) 0s, color 0.2s linear;
  -moz-transition: -moz-transform 0.35s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.35s cubic-bezier(0, 0, 0.25, 1) 0s,
      color 0.2s linear;
  -o-transition: -o-transform 0.35s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.35s cubic-bezier(0, 0, 0.25, 1) 0s,
      color 0.2s linear;
  transition: transform 0.35s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.35s cubic-bezier(0, 0, 0.25, 1) 0s,
      color 0.2s linear;
}

.lg-hide-items {
  .lg-prev {
      opacity: 0;
      -webkit-transform: translate3d(-10px, 0, 0);
      transform: translate3d(-10px, 0, 0);
  }

  .lg-next {
      opacity: 0;
      -webkit-transform: translate3d(10px, 0, 0);
      transform: translate3d(10px, 0, 0);
  }

  .lg-toolbar {
      opacity: 0;
      -webkit-transform: translate3d(0, -10px, 0);
      transform: translate3d(0, -10px, 0);
  }
}

body:not(.lg-from-hash) .lg-outer.lg-start-zoom {
  .lg-object {
      -webkit-transform: scale3d(0.5, 0.5, 0.5);
      transform: scale3d(0.5, 0.5, 0.5);
      opacity: 0;
      -webkit-transition: -webkit-transform 250ms cubic-bezier(0, 0, 0.25, 1) 0s,
          opacity 250ms cubic-bezier(0, 0, 0.25, 1) !important;
      -moz-transition: -moz-transform 250ms cubic-bezier(0, 0, 0.25, 1) 0s, opacity 250ms cubic-bezier(0, 0, 0.25, 1) !important;
      -o-transition: -o-transform 250ms cubic-bezier(0, 0, 0.25, 1) 0s, opacity 250ms cubic-bezier(0, 0, 0.25, 1) !important;
      transition: transform 250ms cubic-bezier(0, 0, 0.25, 1) 0s, opacity 250ms cubic-bezier(0, 0, 0.25, 1) !important;
      -webkit-transform-origin: 50% 50%;
      -moz-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
      transform-origin: 50% 50%;
  }

  .lg-item.lg-complete .lg-object {
      -webkit-transform: scale3d(1, 1, 1);
      transform: scale3d(1, 1, 1);
      opacity: 1;
      border-radius: 5px;
  }
}

.lg-outer {
  .lg-thumb-outer {
      background-color: #0d0a0a;
      bottom: 0;
      position: absolute;
      width: 100%;
      z-index: 1080;
      max-height: 350px;
      -webkit-transform: translate3d(0, 100%, 0);
      transform: translate3d(0, 100%, 0);
      -webkit-transition: -webkit-transform 0.25s cubic-bezier(0, 0, 0.25, 1) 0s;
      -moz-transition: -moz-transform 0.25s cubic-bezier(0, 0, 0.25, 1) 0s;
      -o-transition: -o-transform 0.25s cubic-bezier(0, 0, 0.25, 1) 0s;
      transition: transform 0.25s cubic-bezier(0, 0, 0.25, 1) 0s;

      &.lg-grab .lg-thumb-item {
          cursor: -webkit-grab;
          cursor: -moz-grab;
          cursor: -o-grab;
          cursor: -ms-grab;
          cursor: grab;
      }

      &.lg-grabbing .lg-thumb-item {
          cursor: move;
          cursor: -webkit-grabbing;
          cursor: -moz-grabbing;
          cursor: -o-grabbing;
          cursor: -ms-grabbing;
          cursor: grabbing;
      }

      &.lg-dragging .lg-thumb {
          -webkit-transition-duration: 0s !important;
          transition-duration: 0s !important;
      }
  }

  &.lg-thumb-open .lg-thumb-outer {
      -webkit-transform: translate3d(0, 0%, 0);
      transform: translate3d(0, 0%, 0);
  }

  .lg-thumb {
      padding: 10px 0;
      height: 100%;
      margin-bottom: -5px;
  }

  .lg-thumb-item {
      border-radius: 5px;
      cursor: pointer;
      float: left;
      overflow: hidden;
      height: 100%;
      border: 2px solid $white;
      border-radius: 4px;
      margin-bottom: 5px;

      &.active,
      &:hover {
          border-color: #a90707;
      }

      img {
          width: 100%;
          height: 100%;
          object-fit: cover;
      }
  }

  &.lg-has-thumb .lg-item {
      padding-bottom: 120px;
  }

  &.lg-can-toggle .lg-item {
      padding-bottom: 0;
  }

  &.lg-pull-caption-up {
      .lg-sub-html {
          -webkit-transition: bottom 0.25s ease;
          -o-transition: bottom 0.25s ease;
          transition: bottom 0.25s ease;
      }

      &.lg-thumb-open .lg-sub-html {
          bottom: 100px;
      }
  }

  .lg-toggle-thumb {
      background-color: #0d0a0a;
      border-radius: 2px 2px 0 0;
      color: #999;
      cursor: pointer;
      font-size: 24px;
      height: 39px;
      line-height: 27px;
      padding: 5px 0;
      position: absolute;
      right: 20px;
      text-align: center;
      top: -39px;
      width: 50px;

      &:after {
          content: "\e1ff";
      }

      &:hover {
          color: $white;
      }
  }

  .lg-video-cont {
      display: inline-block;
      vertical-align: middle;
      max-width: 1140px;
      max-height: 100%;
      width: 100%;
      padding: 0 5px;
  }

  .lg-video {
      width: 100%;
      height: 0;
      padding-bottom: 56.25%;
      overflow: hidden;
      position: relative;

      .lg-object {
          display: inline-block;
          position: absolute;
          top: 0;
          left: 0;
          width: 100% !important;
          height: 100% !important;
      }

      .lg-video-play {
          width: 84px;
          height: 59px;
          position: absolute;
          left: 50%;
          top: 50%;
          margin-left: -42px;
          margin-top: -30px;
          z-index: 1080;
          cursor: pointer;
      }
  }

  .lg-has-html5 {
      .lg-video-play {
          height: 64px;
          margin-left: -32px;
          margin-top: -32px;
          width: 64px;
          opacity: 0.8;
      }

      &:hover .lg-video-play {
          opacity: 1;
      }
  }

  .lg-video-object {
      width: 100% !important;
      height: 100% !important;
      position: absolute;
      top: 0;
      left: 0;
  }

  .lg-has-video {
      .lg-video-object {
          visibility: hidden;
      }

      &.lg-video-playing {
          .lg-object,
          .lg-video-play {
              display: none;
          }

          .lg-video-object {
              visibility: visible;
          }
      }
  }
}

@media (min-width: 1025px) {
  .lg-outer .lg-thumb-item {
      -webkit-transition: border-color 0.25s ease;
      -o-transition: border-color 0.25s ease;
      transition: border-color 0.25s ease;
