.stop-scrolling {
    height: 100%;
  
    /* overflow: hidden; */
  }
  
  .sweet-overlay {
    background-color: black;
  
    /* IE8 */
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=40)";
  
    /* IE8 */
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: none;
    z-index: 10000;
  }
  
  .sweet-alert {
    background-color: white;
    width: 478px;
    padding: 40px;
    text-align: center;
    position: fixed;
    left: 50%;
    top: 50%;
    margin-left: -256px;
    margin-top: -200px;
    overflow: hidden;
    display: none;
    z-index: 99999;
    border-radius: 5px;
  
    h2 {
      color: #25252a;
      font-size: 30px;
      text-align: center;
      font-weight: 600;
      text-transform: none;
      position: relative;
      display: block;
      margin-bottom: 5px;
    }
  
    p {
      color: #76839a;
      font-size: 16px;
      text-align: center;
      font-weight: 300;
      position: relative;
      text-align: inherit;
      float: none;
      line-height: normal;
    }
  
    fieldset {
      border: none;
      position: relative;
      display: none;
    }
  
    .sa-error-container {
      background-color: #f1f1f1;
      margin-left: -17px;
      margin-right: -17px;
      overflow: hidden;
      padding: 0 10px;
      max-height: 0;
      webkit-transition: padding 0.15s, max-height 0.15s;
      transition: padding 0.15s, max-height 0.15s;
  
      &.show {
        padding: 10px 0;
        max-height: 100px;
        webkit-transition: padding 0.2s, max-height 0.2s;
        transition: padding 0.25s, max-height 0.25s;
      }
  
      .icon {
        display: inline-block;
        width: 24px;
        height: 24px;
        border-radius: 50%;
        background-color: #ea7d7d;
        color: white;
        line-height: 24px;
        text-align: center;
        margin-right: 3px;
      }
  
      p {
        display: inline-block;
      }
    }
  
    .sa-input-error {
      position: absolute;
      top: 29px;
      right: 26px;
      width: 20px;
      height: 20px;
      opacity: 0;
      -webkit-transform: scale(0.5);
      transform: scale(0.5);
      -webkit-transform-origin: 50% 50%;
      transform-origin: 50% 50%;
      -webkit-transition: all 0.1s;
      transition: all 0.1s;
  
      &::before, &::after {
        content: "";
        width: 20px;
        height: 6px;
        background-color: #f06e57;
        border-radius: 3px;
        position: absolute;
        top: 50%;
        margin-top: -4px;
        left: 50%;
        margin-left: -9px;
      }
  
      &::before {
        -webkit-transform: rotate(-45deg);
        transform: rotate(-45deg);
      }
  
      &::after {
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
      }
  
      &.show {
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1);
      }
    }
  
    input {
      width: 100%;
      box-sizing: border-box;
      border-radius: 3px;
      border: 1px solid #d7d7d7;
      height: 43px;
      margin-top: 10px;
      margin-bottom: 17px;
      font-size: 18px;
      box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.06);
      padding: 0 12px;
      display: none;
      -webkit-transition: all 0.3s;
      transition: all 0.3s;
  
      &:focus {
        outline: none;
        box-shadow: 0px 0px 3px #c4e6f5;
        border: 1px solid #b4dbed;
  
        &::-moz-placeholder, &:-ms-input-placeholder, &::-webkit-input-placeholder {
          transition: opacity 0.3s 0.03s ease;
          opacity: 0.5;
        }
      }
  
      &::-moz-placeholder, &:-ms-input-placeholder, &::-webkit-input-placeholder {
        color: #bdbdbd;
      }
    }
  
    &.show-input input {
      display: block;
    }
  
    .sa-confirm-button-container {
      display: inline-block;
      position: relative;
    }
  
    .la-ball-fall {
      position: absolute;
      left: 50%;
      top: 50%;
      margin-left: -27px;
      margin-top: 4px;
      opacity: 0;
      visibility: hidden;
    }
  
    button {
      background-color: $primary-1 !important;
      color: white;
      letter-spacing: .03em;
      font-size: 0.8125rem;
      min-width: 2.375rem;
      display: inline-block;
      font-weight: 400;
      text-align: center;
      white-space: nowrap;
      vertical-align: middle;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      border: 1px solid transparent;
      padding: 0.375rem 0.75rem;
      line-height: 1.84615385;
      border-radius: 5px;
      transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
      cursor: pointer;
      box-shadow: none;
  
      &:focus {
        outline: none;
        box-shadow: 0 0 2px rgba(128, 179, 235, 0.5), inset 0 0 0 1px rgba(0, 0, 0, 0.05);
      }
  
      &:hover, &:active {
        background-color: #0061da;
      }
  
      &.cancel {
        background-color: #C1C1C1;
  
        &:hover {
          background-color: #b9b9b9;
        }
  
        &:active {
          background-color: #a8a8a8;
        }
  
        &:focus {
          box-shadow: rgba(197, 205, 211, 0.8) 0px 0px 2px, rgba(0, 0, 0, 0.0470588) 0px 0px 0px 1px inset !important;
        }
      }
  
      &[disabled] {
        opacity: .6;
        cursor: default;
      }
  
      &.confirm[disabled] {
        color: transparent;
  
        ~ .la-ball-fall {
          opacity: 1;
          visibility: visible;
          transition-delay: 0s;
        }
      }
  
      &::-moz-focus-inner {
        border: 0;
      }
    }
  
    &[data-has-cancel-button=false] button {
      box-shadow: none !important;
    }
  
    &[data-has-confirm-button=false][data-has-cancel-button=false] {
      padding-bottom: 40px;
    }
  
    .sa-icon {
      width: 60px;
      height: 60px;
      border: 4px solid #45aaf2;
      -webkit-border-radius: 40px;
      border-radius: 40px;
      border-radius: 50%;
      margin: 20px auto;
      padding: 0;
      position: relative;
      box-sizing: content-box;
  
      &.sa-error {
        border-color: #f5334f;
  
        .sa-x-mark {
          position: relative;
          display: block;
        }
  
        .sa-line {
          position: absolute;
          height: 4px;
          width: 28px;
          background-color: #f5334f;
          display: block;
          top: 28px;
          border-radius: 2px;
  
          &.sa-left {
            -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
            left: 17px;
          }
  
          &.sa-right {
            -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
            right: 15px;
          }
        }
      }
  
      &.sa-warning {
        border-color: #f7b731;
  
        .sa-body {
          position: absolute;
          width: 5px;
          height: 30px;
          left: 50%;
          top: 10px;
          -webkit-border-radius: 2px;
          border-radius: 2px;
          margin-left: -2px;
          background-color: #f7b731;
        }
  
        .sa-dot {
          position: absolute;
          width: 7px;
          height: 7px;
          -webkit-border-radius: 50%;
          border-radius: 50%;
          margin-left: -3px;
          left: 50%;
          bottom: 10px;
          background-color: #f7b731;
        }
      }
  
      &.sa-info {
        border-color: #45aaf2;
  
        &::before {
          content: "";
          position: absolute;
          width: 4px;
          height: 18px;
          top: 28px;
          left: 50%;
          bottom: 17px;
          border-radius: 2px;
          margin-left: -2px;
          background-color: #45aaf2;
        }
  
        &::after {
          content: "";
          position: absolute;
          width: 6px;
          height: 6px;
          border-radius: 50%;
          margin-left: -3px;
          top: 19px;
          background-color: #45aaf2;
        }
      }
  
      &.sa-success {
        border-color: #09ad95;
  
        &::before, &::after {
          content: '';
          -webkit-border-radius: 40px;
          border-radius: 40px;
          border-radius: 50%;
          position: absolute;
          width: 60px;
          height: 120px;
          -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
        }
  
        &::before {
          -webkit-border-radius: 120px 0 0 120px;
          border-radius: 120px 0 0 120px;
          top: -7px;
          left: -33px;
          -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
          -webkit-transform-origin: 60px 60px;
          transform-origin: 60px 60px;
        }
  
        &::after {
          -webkit-border-radius: 0 120px 120px 0;
          border-radius: 0 120px 120px 0;
          top: -11px;
          left: 30px;
          -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
          -webkit-transform-origin: 0px 60px;
          transform-origin: 0px 60px;
        }
  
        .sa-placeholder {
          width: 60px;
          height: 60px;
          border: 0px solid rgba(165, 220, 134, 0.2);
          -webkit-border-radius: 40px;
          border-radius: 40px;
          border-radius: 50%;
          box-sizing: border-box;
          position: absolute;
          z-index: 2;
        }
  
        .sa-fix {
          width: 5px;
          height: 90px;
          position: absolute;
          left: 28px;
          top: 8px;
          z-index: 1;
          -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
        }
  
        .sa-line {
          height: 5px;
          background-color: #09ad95;
          display: block;
          border-radius: 2px;
          position: absolute;
          z-index: 2;
  
          &.sa-tip {
            width: 12px;
            left: 18px;
            top: 34px;
            -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
          }
  
          &.sa-long {
            width: 25px;
            right: 13px;
            top: 29px;
            -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
          }
        }
      }
  
      &.sa-custom {
        background-size: contain;
        border-radius: 0;
        border: none;
        background-position: center center;
        background-repeat: no-repeat;
      }
    }
  }
  
  @media all and (max-width: 540px) {
    .sweet-alert {
      width: auto;
      margin-left: 0;
      margin-right: 0;
      left: 15px;
      right: 15px;
    }
  }
  
